<template>
    <b-container fluid>
        <b-row class="lowered" align-h="center">
            <h1>Logitech Input Device Spy</h1>
        </b-row>
        <b-row class="lowered uppered" align-h="center">
            <b-button v-if="!spy.connected" class="btn btn-kohle" @click="spy.connect()">Connect to server</b-button>
            <b-button v-else @click="spy.disconnect()">Disconnect from server</b-button>
        </b-row>
        <b-row align-h="center">
            <b-alert variant="success" :show="spy.connected">Connected</b-alert>
            <b-alert variant="danger" :show="!spy.connected">Disconnected</b-alert>
        </b-row>
        <b-row class="text-center" align-h="center" v-if="spy.connected" role="tablist">
            <b-card no-body class="mb-1" style="width:95%">
                <b-card-header header-tag="header" class="p-1" role="tab">
                    <b-button block href="#" v-b-toggle.devices variant="info">Devices</b-button>
                </b-card-header>
                <b-collapse id="devices" visible accordion="spy-accordion" role="tabpanel">
                    <b-card-body>
                        <b-tabs card fill>
                            <b-tab v-for="device of spy.devices" :key="device.unitId" :title="device.name" :style="device.isConnected ? 'color:black' : 'color:red'">
                                <b-row style="background:lightblue">
                                    <b-col class="dashed"><b>Info</b></b-col>
                                    <b-col class="dashed"><b>Spy Config</b></b-col>
                                    <b-col class="dashed" v-if="device.hasWheel"><b>Wheel</b></b-col>
                                    <b-col class="dashed" v-if="device.hasThumbWheel"><b>Thumb Wheel</b></b-col>
                                    <b-col class="dashed" v-if="device.hasSpecialKeys" cols="6"><b>Special Keys</b></b-col>
                                </b-row>
                                <b-row>
                                    <b-col class="dashed lowered uppered">
                                        ID: {{ device.unitId}}<br>
                                        Type: {{ device.type}}<br>
                                        Wheel: {{ device.hasWheel }}<br>
                                        Thumb Wheel: {{ device.hasThumbWheel }}<br>
                                        Special Keys: {{ device.hasSpecialKeys }}
                                    </b-col>
                                    <b-col class="dashed lowered uppered">
                                        <b-container fluid v-if="spy.spyConfigs[device.unitId]">
                                            <b-row>
                                                <b-col><b>Keys</b></b-col>
                                                <b-col><input type="checkbox" :disabled="!device.isConnected" v-model="spy.spyConfigs[device.unitId].spyKeys" @change="spy.setSpyConfig(spy.spyConfigs[device.unitId])"></b-col>
                                            </b-row>
                                            <b-row>
                                                <b-col><b>Pointer</b></b-col>
                                                <b-col><input type="checkbox" :disabled="!device.isConnected" v-model="spy.spyConfigs[device.unitId].spyPointer" @change="spy.setSpyConfig(spy.spyConfigs[device.unitId])"></b-col>
                                            </b-row>
                                            <b-row>
                                                <b-col><b>Wheel</b></b-col>
                                                <b-col><input type="checkbox" :disabled="!device.isConnected" v-model="spy.spyConfigs[device.unitId].spyWheel" @change="spy.setSpyConfig(spy.spyConfigs[device.unitId])"></b-col>
                                            </b-row>
                                            <b-row>
                                                <b-col><b>Thumb Wheel</b></b-col>
                                                <b-col><input type="checkbox" :disabled="!device.isConnected" v-model="spy.spyConfigs[device.unitId].spyThumbWheel" @change="spy.setSpyConfig(spy.spyConfigs[device.unitId])"></b-col>
                                            </b-row>
                                        </b-container>
                                    </b-col>
                                    <b-col class="dashed lowered uppered" v-if="device.hasWheel">
                                        <b-container fluid v-if="spy.wheelConfigs[device.unitId]">
                                            <b-row>
                                                <b-col><b>Divert</b></b-col>
                                                <b-col><input type="checkbox" :disabled="!device.isConnected" v-model="spy.wheelConfigs[device.unitId].divert" @change="spy.setWheelConfig(spy.wheelConfigs[device.unitId])"></b-col>
                                            </b-row>
                                            <b-row>
                                                <b-col><b>Invert</b></b-col>
                                                <b-col><input type="checkbox" :disabled="!device.isConnected" v-model="spy.wheelConfigs[device.unitId].invert" @change="spy.setWheelConfig(spy.wheelConfigs[device.unitId])"></b-col>
                                            </b-row>
                                            <b-row>
                                                <b-col><b>Hi-Res</b></b-col>
                                                <b-col><input type="checkbox" :disabled="!device.isConnected" v-model="spy.wheelConfigs[device.unitId].hires" @change="spy.setWheelConfig(spy.wheelConfigs[device.unitId])"></b-col>
                                            </b-row>
                                        </b-container>
                                    </b-col>
                                    <b-col class="dashed lowered uppered" v-if="device.hasThumbWheel">
                                        <b-container fluid v-if="spy.thumbWheelConfigs[device.unitId]">
                                            <b-row>
                                                <b-col><b>Divert</b></b-col>
                                                <b-col><input type="checkbox" :disabled="!device.isConnected" v-model="spy.thumbWheelConfigs[device.unitId].divert" @change="spy.setThumbWheelConfig(spy.thumbWheelConfigs[device.unitId])"></b-col>
                                            </b-row>
                                            <b-row>
                                                <b-col><b>Invert</b></b-col>
                                                <b-col><input type="checkbox" :disabled="!device.isConnected" v-model="spy.thumbWheelConfigs[device.unitId].invert" @change="spy.setThumbWheelConfig(spy.thumbWheelConfigs[device.unitId])"></b-col>
                                            </b-row>
                                        </b-container>
                                    </b-col>
                                    <b-col class="dashed lowered uppered" cols="6" v-if="device.hasSpecialKeys">
                                        <b-container fluid v-if="spy.specialKeyConfigs[device.unitId]">
                                            <b-row>
                                                <b-col><b>Control ID</b></b-col>
                                                <b-col><b>Type</b></b-col>
                                                <b-col><b>F Pos</b></b-col>
                                                <b-col><b>Fn Toggle</b></b-col>
                                                <b-col><b>Divert</b></b-col>
                                                <b-col><b>Raw XY</b></b-col>
                                                <b-col><b>Force Raw XY</b></b-col>
                                            </b-row>
                                            <b-row v-for="config of spy.specialKeyConfigs[device.unitId]" :key="config.controlId">
                                                <b-col>{{ config.controlId }}</b-col>
                                                <b-col>{{ spy.specialKeys[config.unitId][config.controlId].type }}</b-col>
                                                <b-col>{{ spy.specialKeys[config.unitId][config.controlId].fpos }}</b-col>
                                                <b-col>{{ spy.specialKeys[config.unitId][config.controlId].fntog }}</b-col>
                                                <b-col><input v-if="spy.specialKeys[config.unitId][config.controlId].divert" type="checkbox" :disabled="!device.isConnected" v-model="config.divert" @change="spy.setSpecialKeyConfig(config)"></b-col>
                                                <b-col><input v-if="spy.specialKeys[config.unitId][config.controlId].rawXY" type="checkbox" :disabled="!device.isConnected" v-model="config.rawXY" @change="spy.setSpecialKeyConfig(config)"></b-col>
                                                <b-col><input v-if="spy.specialKeys[config.unitId][config.controlId].forceRawXY" type="checkbox" :disabled="!device.isConnected" v-model="config.forceRawXY" @change="spy.setSpecialKeyConfig(config)"></b-col>
                                            </b-row>
                                        </b-container>
                                    </b-col>
                                </b-row>
                            </b-tab>
                        </b-tabs>
                    </b-card-body>
                </b-collapse>
            </b-card>
            <b-card no-body class="mb-1" style="width:95%">
                <b-card-header header-tag="header" class="p-1" role="tab">
                    <b-button block href="#" v-b-toggle.event-log variant="info">Event Log</b-button>
                </b-card-header>
                <b-collapse id="event-log" accordion="spy-accordion" role="tabpanel">
                    <b-card-body>
                        <b-button class="btn" style="margin-bottom:1rem" @click="spy.events = []">Clear Log</b-button>
                        <div class="overflow-auto">
                            <b-pagination
                                v-model="currentPage"
                                :total-rows="rows"
                                :per-page="perPage"
                                aria-controls="events-table"
                                limit="20">
                            </b-pagination>
                            <p class="mt-3">Current Page: {{ currentPage }}</p>
                            <b-table id="events-table"
                                :items="spy.events"
                                :current-page="currentPage"
                                :per-page="perPage"
                                small>
                            </b-table>
                        </div>
                    </b-card-body>
                </b-collapse>
            </b-card>
        </b-row>
    </b-container>
</template>

<style lang="scss">

@import "./scss/app-styles.scss";

.card-header {
    background: white
}

.dashed {
    border-width:1px;
    border-color: black;
    border-style: dashed;
}

.lowered {
  padding-top:1rem;
}

.uppered {
  padding-bottom:1rem;
}

</style>

<script>

import { Spy } from './model/Spy';

export default {

   data() {

        return {
            currentPage: 1,
            perPage: 10,
            spy: new Spy()
        }
    },
    computed: {
      rows() {
        return this.spy.events.length
      }
    }
}

</script>
