'use strict';

/**
 * Clears Array content.
 *
 * @extends(Array)
 */
Array.prototype.clear = function() {
    while (this.length > 0) {
        this.pop();
    }
};

/**
 * Looks for a value.
 *
 * @extends(Array)
 * @param {*} value - Value looked for.
 * @returns {Boolean} true if value is found.
 */
Array.prototype.contains = function(value) {

    const index = this.indexOf(value);

    return index !== -1;
};

/**
 * Removes value at index.
 *
 * @extends(Array)
 * @param {Number} index - Index of value to be removed.
 * @returns {*} Removed value if index valid, null otherwise.
 */
Array.prototype.removeAt = function(index) {

    const removedItems = this.splice(index, 1);

    if (removedItems.length > 0) {
        return removedItems[0];
    } else {
        return null;
    }
};

/**
 *  Removes a value.
 *
 * @extends(Array)
 * @param {*} value - Value to remove.
 * @returns {*} Removed value if found, null otherwise.
 */
Array.prototype.remove = function(value) {

    const index = this.indexOf(value);

    if (index !== -1) {
        return this.removeAt(index);
    } else {
        return null;
    }
};

/**
 * Removes first value matching predicate.
 *
 * @extends(Array)
 * @param {function} predicate - Predicate.
 * @returns {*} Removed value if found, null otherwise.
 */
Array.prototype.removeFirst = function(predicate) {

    const removedItems = this.filter(predicate);

    const removedItem = removedItems.shift();
    if (removedItem) {
        this.remove(removedItem);
    }

    return removedItem;
};

/**
 * Removes all values matching predicate.
 *
 * @extends(Array)
 * @param {function} predicate - Predicate.
 * @returns {*} Removed value if found, null otherwise.
 */
Array.prototype.removeWhere = function(predicate) {

    const removedItems = this.filter(predicate);

    removedItems.forEach((item) => {
        this.remove(item);
    });

    return removedItems;
};
